import React, { useState, useEffect } from "react";
import Link from "next/link";
import styles from "../styles/QuestionCard.module.css";

const QuestionCard = ({ question, userInfo }) => {
  const { id, title, tags, voteCount, description } = question;
  const [snippet, setSnippet] = useState("");

  // Function to strip HTML tags from description and get snippet
  const getSnippet = (html) => {
    if (!html) return "";
    const div = document.createElement("div");
    div.innerHTML = html;
    const text = div.textContent || div.innerText || "";
    return text.length > 150 ? text.substring(0, 147) + "..." : text;
  };

  useEffect(() => {
    setSnippet(getSnippet(description));
  }, [description]);

  return (
    <div className={styles.card}>
      <div className={styles.votesSection}>
        <button className={styles.voteButton} aria-label="Upvote">▲</button>
        <div className={styles.voteCount}>{voteCount || 0}</div>
        <button className={styles.voteButton} aria-label="Downvote">▼</button>
      </div>
      <div className={styles.contentSection}>
        <div className={styles.userInfo}>
          {userInfo?.iconUrl && (
            <img src={userInfo.iconUrl} alt="User Icon" className={styles.userIcon} />
          )}
          <span className={styles.username}>{userInfo?.username || "Unknown"}</span>
        </div>
        <Link href={`/question/${id}`} className={styles.title}>
          {title}
        </Link>
        <p className={styles.snippet}>{snippet}</p>
        <div className={styles.tags}>
          {tags && tags.map((tag) => (
            <span key={tag} className={styles.tag}>
              {tag}
            </span>
          ))}
        </div>
      </div>
    </div>
  );
};

export default QuestionCard;
