import React, { useEffect, useState } from "react";
import { collection, query, orderBy, onSnapshot, doc, getDoc } from "firebase/firestore";
import { db } from "../firebase/config";
import QuestionCard from "../components/QuestionCard";
import styles from "../styles/Home.module.css";

const defaultQuestions = [
  {
    id: "default1",
    title: "How to learn React effectively?",
    tags: ["react", "javascript", "frontend"],
    voteCount: 10,
    description: "I am new to React. What are the best resources and practices to learn React effectively?",
  },
  {
    id: "default2",
    title: "What is Firebase and how to use it?",
    tags: ["firebase", "backend", "database"],
    voteCount: 8,
    description: "Can someone explain what Firebase is and how to integrate it with a React app?",
  },
  {
    id: "default3",
    title: "How to manage state in React?",
    tags: ["react", "state-management", "hooks"],
    voteCount: 5,
    description: "What are the different ways to manage state in React applications?",
  },
];

const Home = () => {
  const [questions, setQuestions] = useState(defaultQuestions);
  const [userInfos, setUserInfos] = useState({});
  const [error, setError] = useState(null);

  useEffect(() => {
    const q = query(collection(db, "questions"), orderBy("createdAt", "desc"));
    const unsubscribe = onSnapshot(
      q,
      async (querySnapshot) => {
        const questionsData = [];
        const userInfoMap = {};
        for (const docSnap of querySnapshot.docs) {
          const questionData = { id: docSnap.id, ...docSnap.data() };
          questionsData.push(questionData);
          // Fetch user info for each question
          if (questionData.userId && !userInfoMap[questionData.userId]) {
            const userDoc = await getDoc(doc(db, "users", questionData.userId));
            if (userDoc.exists()) {
              userInfoMap[questionData.userId] = userDoc.data();
            } else {
              userInfoMap[questionData.userId] = { username: "Unknown", iconUrl: null };
            }
          }
        }
        if (questionsData.length > 0) {
          setQuestions(questionsData);
          setUserInfos(userInfoMap);
        }
        setError(null);
        console.log("Fetched questions:", questionsData);
      },
      (err) => {
        console.error("Error fetching questions:", err);
        setError("Failed to load questions.");
      }
    );

    return () => unsubscribe();
  }, []);

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>StackIt - Questions</h1>
      {error && <p className={styles.error}>{error}</p>}
      {!error && questions.length === 0 && (
        <p className={styles.noQuestions}>
          No questions found. You can <a href="/ask">ask the first question</a>!
        </p>
      )}
      <div className={styles.list}>
        {questions.map((question) => (
          <QuestionCard key={question.id} question={question} userInfo={userInfos[question.userId]} />
        ))}
      </div>
    </div>
  );
};

export default Home;
